/**
 * animation
 * 来源：http://daneden.github.io/animate.css/
 */
@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
    transform: scale3d(1.1, 1.1, 1.1);
  }
  to {
    transform: scale3d(1, 1, 1);
  }
}

@keyframes flipInX {
  from {
    transform: rotateX(90deg);
    opacity: 0;
  }
  40% {
    transform: rotateX(-20deg);
  }
  60% {
    transform: rotateX(10deg);
    opacity: 1;
  }
  80% {
    transform: rotateX(-5deg);
  }
  to {
    transform: rotateX(0);
  }
}

@keyframes flipOutY {
  from {
    transform: rotateY(0);
  }
  30% {
    transform: rotateY(-15deg);
    opacity: 1;
  }
  to {
    transform: rotateY(90deg);
    opacity: 0;
  }
}

@keyframes slideInDown {
  from {
    transform: translate(0, -100%);
    visibility: visible;
  }
  to {
    transform: translate(0, 0);
    visibility: visible;
  }
}

@keyframes slideInUp {
  0% {
    transform: translateY(100%);
    visibility: visible;
  }
  100% {
    transform: translateY(0);
    visibility: visible;
  }
}

@keyframes slideInLeft {
  0% {
    transform: translateX(-100%);
    visibility: visible;
  }
  100% {
    transform: translateX(0);
    visibility: visible;
  }
}

@keyframes slideInRight {
  0% {
    transform: translateX(100%);
    visibility: visible;
  }
  100% {
    transform: translateX(0);
    visibility: visible;
  }
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale(0.3);
  }
  50%,
  100% {
    opacity: 1;
  }
}

@keyframes zoomOut {
  0% {
    opacity: 1;
  }
  50%,
  100% {
    opacity: 0;
    transform: scale(0.3);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadeInTranslucence {
  0% {
    opacity: 0;
  }
  100% {
    opacity: .5;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes formSlidelock {
  0% {
    opacity: .33;
  }
  33.33% {
    opacity: .33;
  }
  33.34% {
    opacity: .66;
  }
  66.66% {
    opacity: .66;
  }
  66.67% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

