.label-radio,
.label-checkbox {
  margin-left: 0.83333rem;
  padding-left: 1.4em;
  position: relative;
}

.label-radio:first-of-type,
.label-checkbox:first-of-type {
  margin-left: 0;
}

.label-radio input,
.label-checkbox input {
  transform: translate(0, -50%);
  position: absolute;
  left: 0;
  top: 0.7em;
  cursor: pointer;
}

.radio-group input[type="radio"],
.checkbox-group input[type="checkbox"] {
  display: none;
}

.radio-group input[type="radio"] + label,
.checkbox-group input[type="checkbox"] + label {
  display: inline-block;
  height: 1em;
  padding-left: 1.4em;
  line-height: 1em;
  position: relative;
}

.radio-group input[type="radio"] + label:not(:first-of-type),
.checkbox-group input[type="checkbox"] + label:not(:first-of-type) {
  margin-left: 0.83333rem;
}

.radio-group input[type="radio"] + label:hover:before,
.checkbox-group input[type="checkbox"] + label:hover:before {
  border-color: #148fcc;
}

.radio-group input[type="radio"] + label:before,
.radio-group input[type="radio"] + label:after,
.checkbox-group input[type="checkbox"] + label:before,
.checkbox-group input[type="checkbox"] + label:after {
  content: '';
  width: 1em;
  height: 1em;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: .3s ease-in-out;
}

.radio-group input[type="radio"] + label:before,
.checkbox-group input[type="checkbox"] + label:before {
